import React from 'react';
import { Typography, Button, Divider } from '@material-ui/core';
import { Elements, CardElement, ElementsConsumer } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { commerce } from '../../lib/commerce';
import Review from './Review';

const stripePromise = loadStripe('pk_test_51OX46lSHpshmCPSJ5tSagRLXzOEHPu8gaQb3dmluduhJGJTl7wspxXjHzg0tUE9EHgruB1dTIOMoqcKW7prZxqRq00cHouQyND');

const PaymentForm = ({ checkoutToken, nextStep, backStep, shippingData, onCaptureCheckout,timeout }) => {
  const handleSubmit = async (event, elements, stripe) => {
    event.preventDefault();

    if (!stripe || !elements) return;

    const cardElement = elements.getElement(CardElement);

    
let orderData;
      try {
         orderData = await commerce.checkout.capture(checkoutToken.id, {
          line_items: checkoutToken.line_items,
          customer: {
            firstname: shippingData.firstName,
            lastname: shippingData.lastName,
            email: shippingData.email,
          },
          shipping: {
            name: 'International',
            street: shippingData.address1,
            town_city: shippingData.city,
            county_state: shippingData.shippingSubdivision,
            postal_zip_code: shippingData.zip,
            country: shippingData.shippingCountry,
          },
          fulfillment: {
            shipping_method: shippingData.shippingOption,
          },
          payment: {
            gateway: 'gway_mwJZeaDe14WAwe', // Specify the Commerce.js test gateway
            test_gateway: {
              card: {
                number: '4242424242424242',
                expiry_month: '02',
                expiry_year: '24',
                cvc: '123',
                postal_zip_code: '94107',
              },
            },
          },
        });

      onCaptureCheckout(checkoutToken.id, orderData);
      timeout();
      nextStep();
    } catch (error) {
      onCaptureCheckout(checkoutToken.id, orderData);
      timeout();
      nextStep();
      console.log('e')
      // Handle the error as needed
    }
  };
  

  return (
    <>
      <Review checkoutToken={checkoutToken} />
      <Divider />
      <Typography variant="h6" gutterBottom style={{ margin: '20px 0' }}>Payment method</Typography>
      <Elements stripe={stripePromise}>
        <ElementsConsumer>{({ elements, stripe }) => (
          <form onSubmit={(e) => handleSubmit(e, elements, stripe)}>
            <CardElement />
            <br /> <br />
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <Button variant="outlined" onClick={backStep}>Back</Button>
              <Button type="submit" variant="contained" disabled={!stripe} color="primary">
                Pay {checkoutToken.subtotal.formatted_with_symbol}
              </Button>
            </div>
          </form>
        )}
        </ElementsConsumer>
      </Elements>
    </>
  );
};

export default PaymentForm;